.advantages__item svg {
  margin-bottom: 25px;
}

.advantages__item h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
}

.advantages__item p {
  font-size: 14px;
  line-height: 20px;
}

.catalog {
  padding: 95px 118px 120px;
}

.catalog__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.catalog__container h2 {
  margin: 0;
  margin-bottom: 60px;
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
