.form {
  padding: 50px 163px;
  background-image: url("../../img/form-background.png");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .form {
    background-image: url("../../img/form-background@2x.webp");
  }
}

.form__container {
  display: flex;
  gap: 144px;
  justify-content: center;
  align-items: center;
  min-height: 555px;
}

.form__container h2 {
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  margin: 0;
  margin-bottom: 30px;
}

.form__container p {
  max-width: 370px;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
}

.form__wrapper {
  width: 403px;
  text-align: center;
  background-color: #011c40;
  box-shadow: 0px 4px 45px rgba(1, 28, 64, 0.15);
  box-sizing: border-box;
  padding: 50px;
}

.form__wrapper p {
  color: #f9fbfd;
  text-align: left;
  margin-bottom: 35px;
}

.field {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.field input {
  min-height: 43px;
  width: 100%;
  padding: 11px 16px;
  cursor: pointer;
}
.field input::placeholder {
  font-size: 14px;
  color: rgba(1, 28, 64, 0.7);
}
.field input:hover {
  outline: 2px solid #ffffff;
}
.field input:focus {
  outline: 2px solid #ffffff;
}

.field__checkbox {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}

.field input[type=checkbox] {
  position: absolute;
  opacity: 0;
  top: 4px;
  left: 0;
  width: 18px;
  height: 18px;
  min-height: 14px;
}

.field span:last-of-type {
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
  vertical-align: middle;
  display: inline-block;
  padding-left: 23px;
}

.field__control-mark {
  width: 18px;
  height: 18px;
  position: absolute;
  top: 4px;
  left: 0;
  background-color: #f9fbfd;
  cursor: pointer;
}

.field input[type=checkbox]:checked + span::before {
  content: "";
  position: absolute;
  top: 6px;
  left: 4px;
  width: 10px;
  height: 6px;
  background-image: url("../img/svg/check.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}

.field button {
  color: #011c40;
  background-color: #f9fbfd;
  min-height: 43px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  cursor: pointer;
}

