}

.contacts__info p {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
  margin-bottom: 45px;
  max-width: 500px;
}

.contacts__list ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-style: normal;
}

.contacts__item {
  padding-left: 28px;
  position: relative;
  margin-bottom: 33px;
}
.contacts__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 22px;
  background-image: url("../img/svg/pin.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}
.contacts__item--tel::before {
  background-image: url("../img/svg/phone.svg");
  height: 18px;
}

.map {
  min-height: 306px;
  position: relative;
}

.map iframe {
  width: 100%;
}

.map__icon {
  position: absolute;
  top: 43%;
  left: 45%;
  width: 18px;
  height: 22px;
}

.main-footer {
  background-color: #011c40;
  padding: 50px 103px;
}

.main-footer__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.main-footer__nav {
  padding-left: 40px;
  max-width: 650px;
}

.main-footer__social {
  margin-left: auto;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  max-width: 400px;
}

.social__item {
  padding: 10px;
}

.social__link svg {
  fill: #f9fbfd;
}

